import axios from 'axios';


const loader = axios.create({
    baseURL: 'https://pricesearcher-frontend-test.herokuapp.com/',
    headers: {'x-api-key': '46c0a1e171c76bb37784d60aad4df750'}
});

// const getCustomer = (id) => {
