const getData = (id) => {
    return {
        type: 'FETCH_DATA',
        payload: axios.all([getProducts(id)/*, getOrders(id), getPrescriptions(id)*/])
    }
};



export { getData, getProducts/*, getCustomer, getOrders*/ };


